.post-container {
    margin: 20px auto;
    width: 65%;
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 수직 중앙 정렬 */
}

.post-content {
    border: 3px solid #bbb;
    border-radius: 4px;
    background-color: #fff;
    padding: 15px;
    margin-bottom: 20px;
}

.post-title {
    font-weight: bold;
    margin-bottom: 10px;
}

.post-author {
    margin-bottom: 5px;
    padding-top: 5px;
    color: #666;
}
.commentBtnForm{
    margin: 0;
    padding: 0;
}

.commentDeleteBtn{
    margin: 0;
    padding: 3px 5px;
    color: white;
    
    background-color: crimson;
    border: 1.5px solid #940a0a;
    border-radius: 5px;
    margin-left: 5px;
}

.commentDeleteBtn:hover{
    background-color: red;
}

.commentUpdateBtn{
    margin: 0;
    padding: 3px 5px;
    color: white;
    font-weight: bold;
    background-color: #3dc939;
    border: 2px solid #0a9415;
    border-radius: 5px;
}

.commentUpdateBtn:hover{
    background-color: #1aff00;
}


.post-date {
    color: #666;
    font-size: 12px;
}

.post-comments {
    border: 2px solid #bbb;
    border-radius: 4px;
    background-color: #fff;
    padding: 15px;
    margin-bottom: 15px;
}

.comment {
    padding-left: 15px;
    border-left: #bbb 3px solid;
    margin-left: 10px;
    border-top: 1px solid #ddd;
    padding-bottom: 5px;
    margin-bottom: 10px;
}

.bottoms {
    margin: 20px auto;
    display: flex;
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 수직 중앙 정렬 */
    width: 100%;
    gap: 2rem;
}
/* 버튼 스타일 정의 */
.button {
    display: inline;
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 26px;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    outline: none;
    flex: 1; /* 같은 크기로 설정 */
    margin: 0 15px; /* 좌우 간격 설정 */
}
.button:first-child {
    margin-left: 0;
}
.button:last-child {
    margin-right: 0;
}
/* 버튼 호버 효과 */
.button:hover {
    background-color:#83999f;
}

#commentForm{
    display: flex;
}
#commentForm >textarea {
    flex: 5;
}
#commentForm >button{
    flex: 1;
    background-color:#726f71;
    font-size: 20px;
    font-weight: bold;
    margin-left: 15px;

}
#commentForm >button:hover{
    background-color:#333;
}